.header {
  text-transform: uppercase;
  box-sizing: border-box;
  justify-content: space-between;
  align-items: center;
  min-width: 1024px;
  height: 60px;
  padding: 0 50px;
  font-weight: 500;
  transition: transform .3s;
  display: flex;
}

a:hover {
  color: var(--main-color);
}

.nav__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav__link {
  color: #060b35;
  margin-left: 20px;
  font-size: 12px;
  line-height: 60px;
  text-decoration: none;
  display: inline-block;
}

.header__logo {
  width: 40px;
  height: 40px;
  top: 10px;
}

.header__logo:hover {
  border: 2px solid var(--main-color);
}

.is-active {
  color: var(--main-color);
  margin-left: 0;
  position: relative;
}

.is-active:after {
  content: "";
  background: var(--main-color);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
}

@media (width >= 1200px) {
  .header {
    min-width: 1200px;
  }
}

.cards_container {
  grid-template-rows: repeat(2, 406px);
  grid-template-columns: repeat(4, 200px);
  grid-auto-flow: row;
  justify-content: center;
  gap: 48px;
  padding: 50px 40px;
  transition: transform .3s;
  display: grid;
}

.card {
  background-color: #fff;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  width: 198px;
  height: 406px;
  font-family: Roboto, sans-serif;
  transition: transform .3s;
}

.card__image {
  width: 160px;
  height: 134px;
  margin-top: 32px;
  margin-left: 19px;
}

.card__title {
  color: #060b35;
  width: 166px;
  height: 36px;
  margin: 36px 16px 4px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card__code {
  color: #616070;
  white-space: nowrap;
  margin: 0 84px 18px 16px;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.card__block {
  white-space: nowrap;
  justify-content: space-between;
  margin-left: 18px;
  margin-right: 16px;
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
  margin-bottom: 8px;
  display: inline-block;
}

.stars__star:nth-child(-n+4) {
  background-image: url("star-active.edda4c43.svg");
}

.card__reviews {
  text-align: right;
  color: #060b35;
  white-space: nowrap;
  margin-left: 17px;
  font-size: 10px;
  line-height: 14px;
}

.card__price--container {
  justify-content: space-between;
  display: flex;
}

.card__price--title {
  color: #616070;
  margin-left: 16px;
  font-size: 12px;
  font-weight: 400;
  line-height: 18px;
}

.card__price--number {
  text-align: right;
  color: #060b35;
  margin-top: 10px;
  margin-right: 16px;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.card__button {
  background-color: var(--main-color);
  color: #fff;
  text-align: center;
  border: none;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  width: 166px;
  height: 40px;
  margin-left: 16px;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  display: flex;
}

.card__button:hover {
  color: #00acdc;
  cursor: pointer;
  background-color: #fff;
  border: 1px solid #00acdc;
}

.card:hover {
  color: var(--secondary-color);
  transform: scale(1.2);
}

.card__title:hover {
  color: var(--secondary-color);
}

@media (width <= 487px) {
  .cards_container {
    grid-template-columns: repeat(1, 200px);
  }
}

@media (width >= 488px) {
  .cards_container {
    grid-template-columns: repeat(2, 200px);
  }
}

@media (width >= 768px) {
  .cards_container {
    grid-template-columns: repeat(3, 200px);
  }
}

@media (width >= 1024px) {
  .cards_container {
    grid-template-columns: repeat(4, 200px);
  }
}

html {
  --main-color: #00acdc;
  --secondary-color: #34568b;
  margin: 0;
  font-family: Roboto, sans-serif;
}

body {
  margin: 0;
}
